body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f6f5f7;
}

html {
  font-size: 15px;
}
.bubbleBackground, .root:before, .root:after {
  content: '';
  position: absolute;
  top: 0;
  width: 50%;
  height: 40%;
  background-repeat: no-repeat;
  opacity: 1;
  border-radius: 50%;
  left: -10%;
  top: -10%; }

.root {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  background: #ffffff;
  width: 800px;
  max-width: 100%;
  min-height: 520px; }
  .root:before {
    left: 0; }
  .root:after {
    right: 0;
    background-color: orange;
    background-position: 100% -10px; }

.form, .rightContainer, .leftContainer {
  position: absolute;
  top: 0;
  height: 100%; }

.rightContainer {
  -webkit-transition: z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  transition: z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s;
  transition: transform 0.6s ease-in-out, z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  left: 0;
  width: 50%;
  z-index: 2; }

.leftContainer {
  -webkit-transition: z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  transition: z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s;
  transition: transform 0.6s ease-in-out, z-index 0.1s ease-in-out 0.25s, opacity 0.1s ease-in-out 0.25s, -webkit-transform 0.6s ease-in-out;
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1; }

.overlayContainer {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  z-index: 100; }

.rightContainer.right-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  z-index: -1; }

.leftContainer.right-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  -webkit-animation: show 0.6s;
          animation: show 0.6s; }

.overlayContainer.right-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%); }

@-webkit-keyframes show {
  0%,
  49% {
    opacity: 0;
    z-index: 1; }
  50%,
  100% {
    opacity: 1;
    z-index: 5; } }

@keyframes show {
  0%,
  49% {
    opacity: 0;
    z-index: 1; }
  50%,
  100% {
    opacity: 1;
    z-index: 5; } }

