.Task {
  padding: 1rem 1.5rem;
  border-radius: 10px;
  margin: 10px 0;
  align-items: center;
  background: #fff;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.13), 0 1px 3px rgba(0, 0, 0, 0.26); }

.Task__up {
  display: flex;
  align-items: center;
  line-height: 0; }

.Task__down {
  line-height: 0; }

.Items {
  height: 320px;
  margin: 6rem 1rem;
  padding: 0 1rem;
  overflow-y: auto;
  flex: 1 1;
  z-index: 0; }

.content {
  display: block;
  width: 100%; }

.delete {
  color: white;
  background-color: red;
  border: 1px solid red; }

.delete:hover {
  cursor: pointer;
  background-color: #ff3232; }

.overlay {
  background: #00ffbf;
  background: -webkit-linear-gradient(to right, #00ffbf, #008cff);
  background: -webkit-gradient(linear, left top, right top, from(#00ffbf), to(#008cff));
  background: -webkit-linear-gradient(left, #00ffbf, #008cff);
  background: linear-gradient(to right, #00ffbf, #008cff);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out; }

.panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out; }

.panel.left {
  -webkit-transform: translateX(-20%);
          transform: translateX(-20%); }

.panel.right {
  right: 0;
  -webkit-transform: translateX(0);
          transform: translateX(0); }

:global(.right-active) .left {
  -webkit-transform: translateX(0);
          transform: translateX(0); }

:global(.right-active) .right {
  -webkit-transform: translateX(20%);
          transform: translateX(20%); }

:global(.right-active) .overlay {
  -webkit-transform: translateX(50%);
          transform: translateX(50%); }

.NavigationItems {
  margin-top: 4rem; }

.NavigationItem {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    background-color:transparent;
	border-radius:28px;
	border:1px solid #fafafa;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:18px;
	padding:16px 31px;
    text-decoration:none;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    background-color: rgb(134, 201, 226);
}
form {
  margin: 50px;
  margin-top: 30%; }

input {
  width: 100%;
  border-radius: 5px;
