  padding: 10px 5px 10px 5px;
  vertical-align: middle;
  line-height: 30px;
  font-size: .9rem;
  box-shadow: none;
  margin-bottom: 10px; }

.title {
  margin-left: 10%;
  margin-top: 10%;
  color: white; }

.button_new {
  margin: 10% 24%;
  background-color: transparent;
  border-radius: 28px;
  border: 1px solid orange;
  display: inline-block;
  cursor: pointer;
  color: orange;
  font-family: Arial;
  font-size: 18px;
  padding: 16px 31px;
  text-decoration: none; }

.button_new:hover {
  background-color: #fdc65e;
  color: white; }

.button_new:disabled {
